<template>
  <div class="my-address">
    <van-nav-bar title="收货地址" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-address-list
      v-model="aid"
      :list="addressList"
      default-tag-text="默认"
      @add="goAddress"
      @edit="goAddress"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      aid: "1",
      addressList: []
    };
  },
  created() {
    this.getAddressData();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //获取地址数据
    getAddressData() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 20000) {
            result.data.result.forEach(v => {
              let currentAddress = {
                id: v.aid,
                tel: v.tel,
                name: v.name,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault)
              };
              if (v.isDefault) {
                this.aid = v.aid;
              }
              this.addressList.push(currentAddress);
            });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //新增，编辑地址，新增地址不要携带aid，编辑地址需要带aid，aid:地址id
    goAddress(address) {
      let o = {
        name: "Address"
      };
      if (address) {
        o.params = {
          aid: address.id
        };
      }
      this.$router.push(o);
    }
  }
};
</script>

<style lang="less" scoped>
.my-address {
  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: brown;
    border-color: brown;
    color: #fff;
  }
  /deep/ .van-address-item {
    margin-bottom: 20px;
    border-radius: 10px;
    box-shadow: 0 0 5px 5px rgba(215, 180, 148, 0.4);
  }
  /deep/ .van-tag--danger {
    background-image: linear-gradient(rgb(215, 180, 148), brown);
    border-color: none;
  }

  /deep/ .van-button--danger {
    background-image: linear-gradient(rgb(215, 180, 148), brown);
    border: none;
  }

  /deep/ .van-radio__icon {
    display: none;
  }
}
</style>